<template class="THIS">
  <b-tabs id="selector-tabs">
    <!-- Render Tabs, supply a unique `key` to each tab -->
    <b-tab
      v-for="i in tabs"
      :key="'dyn-tab-' + i"
      :title="'Linea ' + i"
      @click="changeTab(i)"
    >
      <div class="selector-container">
        <iframe
          src="/"
          class="selector-frame"
          frameborder="0"
          :id="'frame_' + i"
        ></iframe>

        <b-button
          size="sm"
          variant="danger"
          class="float-right selector-button-close"
          @click="closeTab(i)"
        >
          Chiudi linea {{ i }}
        </b-button>
      </div>
    </b-tab>

    <!-- New Tab Button (Using tabs-end slot) -->
    <template #tabs-end>
      <b-nav-item role="presentation" @click.prevent="newTab">
        <b>+</b>
      </b-nav-item>
    </template>

    <!-- Render this if no tabs -->
    <template #empty>
      <div class="text-center text-muted">
        Non ci sono linee aperte, clicca su + per aprirne una.
      </div>
    </template>
  </b-tabs>
</template>

<script>
import { BTabs, BTab, BNavItem, BButton } from "bootstrap-vue";

export default {
  components: {
    BTabs,
    BTab,
    BNavItem,
    BButton,
  },
  data() {
    return {
      tabs: [1],
      tabCounter: 1,
    };
  },
  methods: {
    closeTab(x) {
      for (let i = 0; i < this.tabs.length; i += 1) {
        if (this.tabs[i] === x) {
          this.tabs.splice(i, 1);
        }
      }
    },
    newTab() {
      this.tabs.push((this.tabCounter += 1));
    },
    changeTab(tab) {
      var frame = document.getElementById("frame_" + tab);
      //console.log(tab, frame);
      /*frame.click();
      frame.focus();*/
      frame.contentWindow.focus(); //focus on active frame to input contents
    },
  },
};
</script>

