  height: 95% !important;
}
#selector-tabs .tab-pane {
  width: 100% !important;
  height: 100% !important;
}
#selector-tabs .tab-content {
  width: 100% !important;
  height: 100% !important;
}
#selector-tabs .nav-link:not([role="tab"]) b {
  font-size: 2em;
  margin-top: -0.4em;
  margin-bottom: -0.4em;
}
.selector-container {
  width: 100%;
  height: 100%;
  min-height: 500px;
  border: none;
}
.selector-frame {
  width: 100%;
  height: 100%;
  border: none;
}
.selector-frame:focus {
  border: 1em solid red;
}

.selector-button-close {
  position: relative;
  bottom: 45px !important;
  left: 10px !important;
